// ============================================================================
//
//    PAGE - CASES
//
// ============================================================================

.cases {
  @media only screen and (max-width: $bp) {
    background: $bg-color--light;
    min-height: calc(100vh - 184px);
  }
}

.new-request {
  @media only screen and (max-width: $bp) {
    display: none;
    &.show {
      display: flex;
    }
  }
  .goback {
    display: none;
    @media only screen and (max-width: $bp) {
      display: flex;
      margin-bottom: $dp * 1.2;
    }
  }

  &__title {
    background: $black-7;
    padding: 0 $dp;
    height: $dp * 3.6;

    p {
      display: flex;
      align-items: center;
      color: white;
      font-size: $s_small;
      font-family: $font-input;
    }
  }

  .group-input {
    color: $white;
    margin-bottom: $dp * 1.2;
  }

  .textarea {
    display: flex;
    flex-direction: column;

    label {
      width: 100%;
      height: $dp * 3.6;
      background: $gray-7;
      padding: 0 $dp;
      display: flex;
      align-items: center;
      margin-bottom: 0;
      color: $white;
    }

    textarea {
      background: $white-2;
      width: 100%;
      height: $dp * 20;
      border: none;
      padding: $dp $dp * 1.5;
      color: $black;
      outline: none;
      font-size: $s_small;
      font-family: $font-input;

      @media only screen and (max-width: $bp) {
        background: $white;
      }

      &.error {
        border: $border-line--error;
        background: $background--error;

        & + .error-text {
          display: flex;
        }
      }
    }
  }

  .group-button {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    @media only screen and (max-width: $bp) {
      display: none;
    }
  }

  .legal-conditions {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: $dp * 2;

    &__text {
      width: calc(100% - 65px);
      padding-right: $dp * 2;
      border-right: $border-line;
    }

    .group-radio {
      width: $dp * 4.5;
      margin: 0;

      .input-radio {
        @media only screen and (max-width: $bp) {
          margin-bottom: $dp;
        }

        &:last-child {
          @media only screen and (max-width: $bp) {
            margin-bottom: 0;
          }
        }
      }
    }
  }
}
