// ============================================================================
//
// COMPONENTS - BUTTONS
//
// ============================================================================
// =============================================================================
//
// Group button Class:
//
// .justify-right
// .justify-center
//
//
// Button Class:
//
// .btn--primary
// .btn--primary-small
// .btn--secondary
// .btn--terciary
// .btn--terciary-small
//
// =============================================================================

// Big buttons style
@mixin btn {
  font-family: 'Life-bold';
  width: 100%;
  max-width: $dp * 36;
  cursor: pointer;
  border: 2px solid $primary-color;
  appearance: none;
  min-height: $dp * 4;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  color: $black-7;
  position: relative;
  text-transform: uppercase;

  @media only screen and (max-width: $bp) {
    max-width: none;
  }

  &:hover {
    color: $black;
    text-decoration: none;
  }

  &:focus {
    outline: none;
    border-radius: 0;
  }

  img {
    display: none;
    width: $dp * 1.8;
    position: absolute;
  }

  &.arrow-left {
    @media only screen and (max-width: $bp) {
      width: 31%;
    }

    img {
      display: block;
      left: $dp * 2;

      @media only screen and (max-width: $bp) {
        left: $dp;
      }
    }
  }

  &.arrow-right {
    @media only screen and (max-width: $bp) {
      width: 65%;
    }
    img {
      display: block;
      right: $dp * 2;
      transform: rotate(180deg);

      @media only screen and (max-width: $bp) {
        right: $dp;
      }
    }
  }
}

.btn--primary {
  @include btn;
  background: $primary-button-bg;

  &:hover {
    background: $primary-button-bg--hover;
    border: 2px solid $primary-button-bg--hover;
  }

  &:disabled {
    background: $primary-button-bg--disabled;
    border: 2px solid $primary-button-bg--disabled;
    color: $primary-button-color--disabled;
    cursor: initial;

    img {
      display: none;
    }
  }
}

.btn--secondary {
  @include btn;
  background: $secondary-button-bg;
  border: 2px solid $black-7;

  &:hover {
    background: $secondary-button-bg--hover;
  }
}

.btn--terciary {
  @include btn;
  background: $terciary-button-bg;
  border: 2px solid $terciary-button-bg;
  color: $white;

  &:hover {
    background: $terciary-button-bg--hover;
    border-color: $terciary-button-bg--hover;
  }
}

// Small buttons style
@mixin btn-small {
  font-family: 'Life-bold';
  font-size: $s_small;
  width: $dp * 16.8;
  cursor: pointer;
  appearance: none;
  min-height: $dp * 2.4;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  color: $black-7;
  position: relative;
  border: none;
  padding: 0 $dp;

  &:focus {
    outline: none;
    border-radius: 0;
  }

  &:hover {
    color: $black;
    text-decoration: none;
  }
}

.btn--primary-small {
  @include btn-small;
  background: $primary-button-bg;

  &:hover {
    background: $primary-button-bg--hover;
  }

  &:disabled {
    background: $primary-button-bg--disabled;
    color: $primary-button-color--disabled;
    cursor: initial;
  }
  &.not-disabled-class:disabled {
    background: $primary-button-bg;
    color: $black-7;
  }
}

.btn--terciary-small {
  @include btn-small;
  background: $terciary-button-bg;

  &:hover {
    background: $terciary-button-bg--hover;
  }
}

// Aligns
.group-button {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  @media only screen and (max-width: $bp) {
    display: none;
  }

  button {
    &:nth-child(2) {
      margin-top: $dp * 2;

      @media only screen and (max-width: $bp) {
        margin-top: 0;
      }
    }
  }

  &.justify-right {
    justify-content: flex-end;
  }

  &.justify-center {
    justify-content: center;
  }

  &.justify-between {
    justify-content: space-between;
  }
}

// Button SWITCH

.switch {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: $dp $dp * 2.5;

  &__label {
    margin: 0;
    width: calc(100% - 100px);
  }

  &__button {
    position: relative;
    display: inline-block;
    width: $dp * 5.2;
    height: $dp * 2.4;

    .slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: $gray-3;
      -webkit-transition: 0.4s;
      transition: 0.4s;

      &:before {
        position: absolute;
        content: '';
        height: $dp * 1.8;
        width: $dp * 1.8;
        left: $dp * 0.3;
        bottom: $dp * 0.3;
        background-color: $white;
        -webkit-transition: 0.4s;
        transition: 0.4s;
      }

      &.round {
        border-radius: 34px;
      }

      &.round:before {
        border-radius: 50%;
      }
    }

    input {
      opacity: 0;

      &:checked + .slider {
        background-color: $yellow;
      }

      &:disabled {
        &:checked + .slider {
          opacity: 0.7;
          cursor: not-allowed;
        }
      }

      &:checked + .slider:before {
        -webkit-transform: translateX($dp * 2.8);
        -ms-transform: translateX($dp * 2.8);
        transform: translateX($dp * 2.8);
      }
    }
  }
}
