// ============================================================================
//
//    PAGE - DEPOSIT
//
// ============================================================================

.deposit {
  display: flex;
  flex-direction: row;

  @media only screen and (max-width: $bp) {
    background: $bg-color--light;
    min-height: calc(100vh - 184px);
  }

  .main-col {
    max-width: calc(100% - 320px);

    &.no-aside {
      max-width: 100%;
    }

    @media only screen and (max-width: 1024px) {
      max-width: 100%;
    }
  }

  .main__body {
    display: flex;
    flex-direction: column;

    &__confirm {
      display: flex;
      flex-direction: column;

      @media only screen and (max-width: $bp) {
        justify-content: center;
        align-items: center;
        text-align: center;
      }

      span {
        font-size: $s_large;
        color: $success-color;
        margin-bottom: $dp;
        display: none;

        @media only screen and (max-width: $bp) {
          display: block;
        }

        & + h2 {
          color: $success-color;

          @media only screen and (max-width: $bp) {
            font-size: $s_small;
          }

          & + h2 {
            font-family: 'Life-regular';

            @media only screen and (max-width: $bp) {
              font-size: $s_normal;
              margin-top: $dp * 3.4;
              font-family: 'Life-bold';
            }
          }
        }
      }

      p {
        font-family: 'Life-light';
        margin: $dp * 4 0;
        font-size: $s_normal;
        width: 100%;
        max-width: $dp * 36;

        @media only screen and (max-width: $bp) {
          margin: $dp * 2 0;
        }
      }

      & + button {
        @media only screen and (max-width: $bp) {
          display: none;
        }
      }
    }
  }

  .aside-col {
    width: $dp * 29;
    height: $dp * 57;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
    display: flex;
    flex-direction: column;
    text-align: right;
    align-items: flex-end;
    padding: $dp * 2;

    @media only screen and (max-width: 1024px) {
      display: none;
    }

    p {
      font-size: $s_tiny;
      text-transform: uppercase;
      margin-bottom: $dp * 0.5;
    }

    h2 {
      font-family: 'Life-regular';
      text-transform: uppercase;
      margin-bottom: $dp * 3;
      font-size: $s_normal;
    }
  }

  // Estilos personalizados de la tabla de Depsitos
  .table {
    &__header {
      p {
        &:first-child {
          width: $dp * 12;
        }

        &:nth-child(2) {
          width: calc(100% - 360px);
        }

        &:nth-child(3) {
          width: $dp * 12;
          text-align: right;
        }

        &:nth-child(4) {
          width: $dp * 12;
          text-align: right;
          padding: 0;
        }
      }
    }

    &__body {
      &__title {
        &__group {
          &:first-child {
            width: $dp * 12;

            @media only screen and (max-width: $bp) {
              display: none;
            }
          }

          &:nth-child(2) {
            width: calc(100% - 240px);

            @media only screen and (max-width: $bp) {
              width: 70%;
            }

            .table__body__title__group__col {
              &:first-child {
                width: calc(100% - 120px);

                @media only screen and (max-width: $bp) {
                  width: 100%;
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                }
              }

              &:last-child {
                width: $dp * 12;
                padding-right: $dp;
                text-align: right;

                @media only screen and (max-width: $bp) {
                  padding: 0;
                  width: 100%;
                  text-align: left;
                  font-size: $s_small;
                  color: $table-color-icon;
                }
              }
            }
          }

          &:nth-child(3) {
            width: $dp * 12;
            text-align: right;

            @media only screen and (max-width: $bp) {
              width: 30%;
            }

            .table__body__title__group__col {
              width: 100%;

              @media only screen and (max-width: $bp) {
                padding: 0;
              }
            }
          }
        }
      }

      &__detail {
        &__row {
          justify-content: space-between;

          &:last-child {
            justify-content: center;
            margin: $dp * 1.5 0 $dp;
            display: flex;
            align-items: center;

            button {
              width: $dp * 12;
              min-height: $dp * 2.4;
              background: transparent;
              display: flex;
              justify-content: center;
              align-items: center;
              border: 1px solid $black;
              font-size: $s_tiny;
              margin: 0 $dp * 1.5;

              &:focus {
                outline: none;
              }

              &:hover {
                background: $gray-3-light;
              }
            }
          }

          p {
            width: 48%;

            &:first-child {
              @media only screen and (max-width: $bp) {
                color: $gray-9;
              }
            }

            &:last-child {
              @media only screen and (max-width: $bp) {
                color: $black-7;
              }
            }
          }
        }
      }

      &__row {
        &.active {
          .table__body__title__group {
            &:nth-child(3) {
              .table__body__title__group__col {
                &:first-child {
                  color: $success-color;
                }
              }
            }
          }
        }

        &.inactive {
          .table__body {
            &__title__group {
              &:nth-child(2) {
                .table__body__title__group__col {
                  &:nth-child(2) {
                    color: $gray-7;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

// Pantallas con formularios de Depsitos
.deposit-form {
  display: flex;
  flex-direction: row;

  @media only screen and (max-width: $bp) {
    background: $white-2;
    min-height: calc(100vh - 184px);
    flex-direction: column;
  }

  .main {
    @media only screen and (max-width: $bp) {
      flex-direction: column-reverse;
    }

    &__body {
      display: flex;
      flex-direction: column;
    }

    .info-text {
      font-size: $s_normal;
    }
  }

  form {
    width: $dp * 36;

    @media only screen and (max-width: $bp) {
      width: 100%;
    }

    .description {
      margin-bottom: $dp * 16;

      @media only screen and (max-width: $bp) {
        margin-bottom: $dp * 3;
      }
    }

    .input-checkbox {
      &:first-child {
        margin-bottom: $dp;
      }

      label {
        margin-bottom: 0;
      }
    }

    .error-text {
      color: $danger-color;
      font-size: $s_tiny;
      font-family: 'Life-regular';

      .ico-alert {
        font-size: $s_tiny;
        margin-right: $dp * 0.5;
      }
    }
  }

  // .group-button {
  //   display: flex;

  //   @media only screen and (max-width: $bp) {
  //     display: none;
  //   }

  //   button {
  //     &:nth-child(2) {
  //       margin-top: $dp * 2;
  //     }
  //   }
  // }

  .main-col {
    width: calc(100% - 390px);

    @media only screen and (max-width: $bp) {
      width: 100%;
    }

    &.no-aside {
      width: 100%;
    }
  }

  .aside-col {
    width: $dp * 36;
    background: none;
    padding: 0;

    @media only screen and (max-width: 1024px) {
      display: flex;
    }

    @media only screen and (max-width: $bp) {
      width: 100%;
      margin-bottom: $dp * 7;
      height: auto;
    }
  }
}

// Pantallas de confirmacin de Depsitos
.deposit-confirm {
  width: $dp * 36;

  @media only screen and (max-width: $bp) {
    width: 100%;
  }

  label {
    margin-bottom: 0;
  }

  p {
    font-family: 'Life-bold';
  }
}
