.switch {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 10px 25px;
}
.switch__label {
  margin: 0;
  width: calc(100% - 100px);
}
.switch__button {
  position: relative;
  display: inline-block;
  width: 52px;
  height: 24px;
}
.switch__button .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #cccccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.switch__button .slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: #ffffff;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.switch__button .slider.round {
  border-radius: 34px;
}
.switch__button .slider.round:before {
  border-radius: 50%;
}
.switch__button input {
  opacity: 0;
}
.switch__button input:checked + .slider {
  background-color: #ffcc33;
}
.switch__button input:disabled:checked + .slider {
  opacity: 0.7;
  cursor: not-allowed;
}
.switch__button input:checked + .slider:before {
  -webkit-transform: translateX(28px);
  -ms-transform: translateX(28px);
  transform: translateX(28px);
}

