// ============================================================================
//
//    COMPONENTS - FORMS
//
// ============================================================================

// General

form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.group-input {
  display: flex;
  font-family: $font-primary;
  width: 100%;
  position: relative;
  margin-bottom: $dp * 2;
  flex-direction: column;
  justify-content: center;
  outline: none;
  .icon-input {
    position: absolute;
    right: $dp;
    bottom: $dp * 1.2;
    &.yellow {
      color: $primary-color;
    }
  }
}

label {
  font-size: $s_normal;
  font-family: 'Life-regular';
  color: $label-color;
  display: inline-flex;
  align-items: center;
  width: 100%;

  @media only screen and (max-width: $bp) {
    font-size: $s_small;
  }
}

// Input FAKE

.input {
  display: flex;
  align-items: center;
  font-size: $s_small;
  border-radius: $border-radius-input;
  border: $border-line;
  background: $background--disabled;
  width: 100%;
  height: $dp * 3.6;
  padding: 0 $dp;
  outline: none;
  font-family: $font-input;
}

// Input TEXT

input[type='text'],
input[type='date'],
input[type='password'] {
  font-size: $s_small;
  border-radius: $border-radius-input;
  border: $border-line;
  width: 100%;
  height: $dp * 3.6;
  padding: 0 $dp;
  outline: none;
  font-family: $font-input;

  &::-webkit-input-placeholder {
    color: $placeholder-color;
  }
  &:-moz-placeholder {
    /* Firefox 18- */
    color: $placeholder-color;
  }
  &::-moz-placeholder {
    /* Firefox 19+ */
    color: $placeholder-color;
  }
  &:-ms-input-placeholder {
    color: $placeholder-color;
  }

  &:focus {
    border: $border-line--focus;
    background: $background--focus;
  }

  &:disabled {
    border: $border-line--disabled;
    background: $white;

    & + label {
      color: $label-color--disabled;
    }

    &::-webkit-input-placeholder {
      color: $placeholder-color--disabled;
    }
    &:-moz-placeholder {
      /* Firefox 18- */
      color: $placeholder-color--disabled;
    }
    &::-moz-placeholder {
      /* Firefox 19+ */
      color: $placeholder-color--disabled;
    }
    &:-ms-input-placeholder {
      color: $placeholder-color--disabled;
    }
  }

  &.error {
    border: $border-line--error;
    background: $background--error;

    & + .error-text {
      display: flex;
      color: $danger-color;
      font-size: $s_tiny;
      font-family: 'Life-regular';
      align-items: baseline;
      margin-top: $dp * 0.5;
      padding-left: 0;

      span {
        margin-right: $dp * 0.5;
        font-size: $s_tiny;
      }
    }
  }
}

