// Estilos componente NG-SELECT
ng-select {
  width: 100%;
  outline: none !important;

  .ng-select-container {
    border-radius: 0 !important;
    font-size: $s_small !important;
    width: 100% !important;
    height: $dp * 3.6 !important;
    border: none !important;
    padding: 0 $dp !important;
    outline: none !important;
    background: $gray-7 !important;
    font-family: $font-input !important;
    -moz-appearance: none !important;
    -webkit-appearance: none !important;
    appearance: none !important;
  }

  .ng-arrow {
    border-color: $white transparent transparent !important;
  }

  .ng-clear-wrapper {
    color: $white !important;
  }

  input {
    height: initial !important;
    padding: 0px !important;
    color: $white !important;
  }

  .ng-value {
    color: $white !important;
  }

  .ng-placeholder {
    color: $white !important;
  }

  .ng-value-container {
    padding: 0 !important;
  }

  .ng-option {
    font-family: $font-input !important;
    border-bottom: 1px solid $border-color !important;

    &:last-child {
      border: none !important;
    }

    &.ng-option-marked {
      background: $primary-color !important;
    }

    &.ng-option-selected {
      background: $primary-color !important;
    }
  }

  &.ng-select-disabled {
    .ng-select-container {
      background: $gray-3 !important;
    }
  }

  &.error {
    .ng-select-container {
      border: $border-line--error !important;
      background: $background--error !important;
      color: $danger-color !important;
    }

    .ng-placeholder {
      color: $danger-color !important;
    }

    .ng-arrow {
      border-color: $danger-color transparent transparent !important;
    }
  }
}

// TEXTAREA
.textarea {
  label {
    font-size: $s_small;
  }
}

// Input CHECKBOX
.input-checkbox {
  position: relative;
  display: flex;
  margin-top: $dp * 0.5;

  &:first-child {
    margin-top: 0;
  }

  .check {
    position: absolute;
    left: $dp * 0.3;
    top: $dp * 0.5;
    display: none;
    font-size: $s_tiny;
    color: $primary-color;

    @media only screen and (max-width: $bp) {
      left: $dp * 0.2;
      top: $dp * 0.4;
      font-size: $s_xtiny;
    }
  }

  .error-text {
    color: $danger-color;
    padding-left: $dp * 2.5;
    font-size: $s_tiny;
    font-family: 'Life-regular';

    .ico-alert {
      font-size: $s_tiny;
      margin-right: $dp * 0.5;
    }
  }
}

input[type='checkbox'],
input[type='checkbox']:before,
input[type='checkbox']:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

input[type='checkbox'] {
  opacity: 0;
  position: absolute;

  & + label {
    font-family: 'Life-regular';
    padding-left: $dp * 2.5;
    display: inline-block;
    vertical-align: middle;

    a {
      text-decoration: underline;

      &:hover {
        color: $black;
      }
    }
  }

  & + label:before {
    position: absolute;
    left: 0;
    top: $dp * 0.1;
    width: $dp * 2;
    height: $dp * 2;
    content: '';
    text-align: center;
    border: $border-line-checkbox;
    border-radius: $border-radius-checkbox;
    z-index: 9;

    @media only screen and (max-width: $bp) {
      width: $dp * 1.6;
      height: $dp * 1.6;
    }
  }

  &:checked {
    & + label:before {
      border: $border-line--focus;
      border-radius: $border-line--focus;
    }

    & ~ .check {
      display: block;
      z-index: 1;
    }
  }

  &:disabled {
    & + label:before {
      border: $border-line--disabled;
    }
  }

  &.error {
    & + label {
      &:before {
        border: $border-line--error;
        background: $background--error;
      }

      a {
        color: $danger-color;

        &:hover {
          color: $danger-color;
        }
      }

      & + .check {
        color: $danger-color;
      }
    }
  }
}

// Input RADIO buttons
.group-radio {
  display: flex;
  flex-direction: column;
}

.input-radio {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  width: $dp * 4.5;
}

input[type='radio'],
input[type='radio']:before,
input[type='radio']:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

input[type='radio'] {
  opacity: 0;
  position: absolute;

  & + label {
    margin: 0;

    &:before {
      position: absolute;
      right: 0;
      border-radius: 50%;
      width: $dp * 1.6;
      height: $dp * 1.6;
      content: '';
      text-align: center;
      border: $border-line-radio;
      z-index: 9;
    }
  }

  &:checked {
    & + label:before {
      border: $border-line-radio--selected;
      background: $primary-color;
      -webkit-box-shadow: inset 0px 0px 0px 2px $white;
      -moz-box-shadow: inset 0px 0px 0px 2px $white;
      box-shadow: inset 0px 0px 0px 2px $white;
    }
  }

  &:disabled {
    & + label:before {
      border: $border-line-radio--disabled;
    }
  }

  &.error {
    & + label {
      color: $danger-color;

      &:before {
        border: $border-line-radio--error;
      }
    }
  }
}
