// ============================================================================
//
//    BASE - GENERAL
//
// ============================================================================

// Mis Peticiones
.cases {
  .tabs-block {
    display: flex;
    flex-direction: column;
    height: 100%;

    &--desktop {
      & ul {
        display: flex;
        flex-direction: row;
        border-bottom: 2px solid $gray-3;

        li {
          margin-bottom: $dp * -0.2;
        }

        .tab,
        .nav-link {
          text-decoration: none;
          padding: $dp * 0.5 $dp * 2.5;
          font-family: 'Read-medium';
          border-right: 1px solid $white;
          display: flex;
          align-items: center;
          border-radius: 0;
          background: $gray-3;

          &:hover {
            background: $gray-3-light;
            color: $black-7;
          }

          &.active {
            background: $white;
            border: 2px solid $gray-3;
            border-bottom: none;
          }
        }
      }

      &__body,
      .tab-content {
        display: flex;
        flex-direction: column;
        border: 2px solid $gray-3;
        border-top: none;
        height: $dp * 40;
        overflow-y: auto;

        &__row {
          display: flex;
          flex-direction: column;
          width: 100%;

          &__title {
            display: flex;
            flex-direction: row;
            cursor: pointer;
            padding: $dp $dp * 2.5;
            align-items: center;
            width: 100%;
            border-bottom: 1px solid $gray-3;

            &:hover {
              background: $yellow;
            }

            .date {
              width: 20%;
            }

            .name {
              width: calc(80% - 14px);
              padding-right: $dp * 2;
            }

            .arrow {
              font-size: $dp * 0.8;
            }
          }

          &__aditional-info {
            display: none;
            padding: $dp $dp * 2.5;
            background: $white-2;
            width: 100%;
            flex-direction: column;
          }

          &.active {
            .arrow {
              transform: rotate(180deg);
            }

            & .tabs-block--desktop__body__row__aditional-info {
              display: flex;
            }
          }
        }
      }

      @media only screen and (max-width: $bp) {
        display: none;
      }
    }

    &--mobile {
      display: none;
      flex-direction: column;

      &__section {
        width: 100%;
        margin-bottom: $dp * 1.2;
        -webkit-box-shadow: 0px 3px 20px -10px rgba(0, 0, 0, 0.5);
        -moz-box-shadow: 0px 3px 20px -10px rgba(0, 0, 0, 0.5);
        box-shadow: 0px 3px 20px -10px rgba(0, 0, 0, 0.5);

        &:last-child {
          .tabs-block--mobile__row {
            p {
              &:last-child {
                color: $black;
              }
            }
          }
        }
      }

      &__title {
        width: 100%;
        background: $gray-7;
        color: $white;
        padding: 0 $dp;
        height: $dp * 3.6;
        display: flex;
        align-items: center;
        font-family: 'Read-medium';
      }

      &__body {
        width: 100%;
        background: $white;
        display: flex;
        flex-direction: column;

        accordion {
          .card {
            border-radius: 0;
            border: 0;

            &-header {
              border-bottom: 2px solid $white-2;
              padding: 0;
            }

            &-body {
              border-bottom: 1px solid $white-2;
              padding: $dp;
            }
          }
        }
      }

      &__row {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border-bottom: 2px solid $white-2;

        &:last-child {
          border: none;
        }

        p {
          padding: $dp;
          width: calc(100% - 100px);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;

          &:last-child {
            color: $success-color;
            width: $dp * 9;
            text-align: right;
            text-overflow: initial;
          }
        }
      }

      @media only screen and (max-width: $bp) {
        display: flex;
      }
    }
  }
}

// Mi Perfil
.personal-data {
  @extend .cases;

  .tabs-block {
    &--desktop {
      display: block;

      & ul {
        @media only screen and (max-width: $bp) {
          background: transparent;
          border: none;
          margin-bottom: $dp * 2;
          justify-content: space-between;

          .nav-item {
            @media only screen and (max-width: $bp) {
              width: 48%;
            }
          }

          .nav-link {
            @media only screen and (max-width: $bp) {
              background: transparent;
              border: $border-line;
              font-family: 'Life-regular';
              text-align: center;
              justify-content: center;
            }

            &.active {
              @media only screen and (max-width: $bp) {
                border: $border-line--focus;
                font-family: 'Life-bold';
              }
            }
          }
        }
      }

      .tab-container {
        margin-bottom: $dp * 3;
      }

      .tab-content {
        height: auto;

        @media only screen and (max-width: $bp) {
          border: none;
          height: auto;
        }

        .tab__personal-data {
          width: 100%;

          &__row {
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: $dp $dp * 2.5;
            border-bottom: 1px solid $border-color;

            @media only screen and (max-width: $bp) {
              border: none;
              padding: $dp 0;
              flex-direction: column;
              align-items: flex-start;
            }

            &:last-child {
              border: none;
            }
          }

          &__col {
            display: flex;

            p {
              @media only screen and (max-width: $bp) {
                font-family: 'Life-light';
              }
            }

            &:first-child {
              width: 30%;
              display: flex;

              @media only screen and (max-width: $bp) {
                width: 100%;
              }
            }

            &:last-child {
              width: 70%;
              display: flex;
              flex-direction: column;

              @media only screen and (max-width: $bp) {
                width: 100%;
              }

              p {
                @media only screen and (max-width: $bp) {
                  font-family: 'Life-regular';
                }
              }
            }
          }

          &__edit {
            display: flex;
            justify-content: space-between;
            width: 100%;

            &__text {
              width: calc(100% - 40px);
              word-break: break-word;
              display: flex;
              align-items: center;
            }

            &__icon {
              cursor: pointer;
            }
          }
        }
      }

      .group-button {
        justify-content: flex-end;

        @media only screen and (max-width: $bp) {
          display: none;
        }
      }

      &__no-data {
        padding: $dp $dp * 2.5;
      }
    }
  }

  .data-protection {
    &__row {
      border-bottom: 1px solid $border-color;

      &:last-child {
        border: none;

        @media only screen and (max-width: $bp) {
          border-bottom: 1px solid $border-color;
        }
      }

      .switch {
        @media only screen and (max-width: $bp) {
          padding: $dp 0;
        }

        &__label {
          @media only screen and (max-width: $bp) {
            width: calc(100% - 70px);
          }

          p {
            @media only screen and (max-width: $bp) {
              font-family: 'Life-light';
            }
          }
        }

        &__button {
          margin-bottom: 0;
        }
      }
    }
  }
}

// Virtual mailbox
.virtual-mailbox {
  @extend .personal-data;

  .tabs-block--desktop__body__row__title {
    @media only screen and (max-width: $bp) {
      padding: 0 $dp * 1.5;
      background: $white;
      height: $dp * 4.5;
      margin-bottom: $dp;
      border-bottom: $border-line--focus;
    }

    .date {
      width: $dp * 9;
    }

    .name {
      width: calc(100% - 106px);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
