// ============================================================================
//
//    HEADER
//
// ============================================================================
header {
  display: flex;
  align-items: center;
  background: $white;
  flex-direction: column;

  @media only screen and (max-width: $bp) {
    height: $dp * 9.6;
    justify-content: flex-end;
  }

  .header {
    &--desktop {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: row;
      position: relative;

      @media only screen and (max-width: $bp) {
        display: none;
      }
    }

    &--mobile {
      display: none;
      justify-content: center;
      align-items: center;

      @media only screen and (max-width: $bp) {
        display: flex;
      }
    }
  }

  .logos {
    width: 100%;
    padding: $dp * 3.6 0;

    .header--desktop {
      a {
        @extend h2;

        &:hover {
          color: initial;
          text-decoration: none;
        }
      }
    }

    @media only screen and (max-width: $bp) {
      padding: 0;
    }

    .logo-desktop {
      width: $dp * 7.6;

      @media only screen and (max-width: $bp) {
        display: none;
      }
    }

    .back-link {
      position: absolute;
      left: $dp * 2.4;
      display: none;
      color: $black;
      text-decoration: none;

      @media only screen and (max-width: $bp) {
        display: flex;
      }

      span {
        transform: rotate(90deg);
        font-size: $dp * 1.8;
        font-weight: bold;
      }
    }

    .menu-mobile {
      position: absolute;
      right: $dp * 2.4;
      display: none;

      &:hover {
        text-decoration: none;
        color: $black;
        outline: none;
      }

      @media only screen and (max-width: $bp) {
        display: flex;
      }

      span {
        font-size: $dp * 1.8;
        font-weight: bold;
      }
    }
  }

  .nav {
    &--desktop {
      width: 100%;
      background: $bg-color--dark;
      padding: $dp * 2 0;
      display: flex;

      @media only screen and (max-width: $bp) {
        display: none;
      }

      .max-size {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
      }

      &__main {
        display: flex;
        flex-direction: row;

        a {
          color: $gray-3;
          font-family: 'Life-regular';
          margin-right: $dp * 5;

          &.active,
          &:hover {
            color: $primary-color;
            text-decoration: none;
          }
        }
      }

      &__secondary {
        display: flex;
        flex-direction: row;

        a {
          margin-left: $dp * 3;
          color: $gray-3;

          .ico-mail {
            font-size: $s_small;
          }

          &.active,
          &:hover {
            text-decoration: none;
            span {
              color: $yellow;
            }
          }
        }
      }
    }

    &--mobile {
      z-index: 999;
      position: fixed;
      top: $dp * 9.6;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba($color: $black, $alpha: 0.66);
      justify-content: flex-end;
      display: none;

      @media only screen and (max-width: $bp) {
        display: flex;
      }

      &__col {
        width: $dp * 28.8;
        background: rgba($color: $black, $alpha: 0.66);
        background-image: url('assets/img/fondo-menu-responsive.png');
        background-repeat: no-repeat;
        background-size: cover;
      }

      &__group {
        margin-top: $dp * 9.6;

        &__link {
          background: $gray-9;
          display: flex;
          flex-direction: row;
          align-items: center;
          height: $dp * 4.8;
          border-bottom: 1px solid $black;
          text-decoration: none;

          &:last-child {
            border: none;
          }

          &:active {
            background: $yellow;
            text-decoration: none;

            span,
            p {
              color: $black;
              text-decoration: none;
            }
          }

          &__ico {
            width: $dp * 7.2;
            display: flex;
            justify-content: center;
            align-items: center;

            span {
              color: $yellow;

              &.ico-euro,
              &.ico-mail {
                font-size: $s_tiny;
              }
            }
          }

          &__text {
            p {
              font-size: $s_normal;
              font-family: 'Read-medium';
              color: $white;
            }
          }
        }
      }
    }
  }
}
