// ============================================================================
//
//    PAGE HELP
//
// ==============================
.help {
  display: flex;
  flex-direction: row;

  @media only screen and (max-width: $bp) {
    background: $bg-color--light;
    min-height: calc(100vh - 96px);
  }

  .main__header__mobile__col {
    h1 {
      margin-bottom: $dp * 2.4;
    }
  }

  .main__header__title__col {
    border: none;
  }

  .main-col__extra-info {
    margin-top: $dp * 2.4;
    font-size: $s_normal;

    @media only screen and (max-width: $bp) {
      text-align: center;
    }

    a {
      color: black;
      text-decoration: underline;
    }
  }

  accordion {
    max-width: $dp * 55.2;
    width: 100%;
    display: flex;
    flex-direction: column;
    font-family: 'Read-regular';

    @media only screen and (max-width: $bp) {
      max-width: 100%;
    }

    &-group {
      margin-bottom: $dp * 1.2;
      display: flex;
      width: 100%;

      .card {
        border-radius: 0;
        border: none;

        &-header {
          padding: 0;
          border-radius: 0;
          border: none;
          background: $bg-color--dark;
          min-height: $dp * 3.6;
          display: flex;
          align-items: center;

          &:before {
            font-family: 'icon-font';
            content: '\e91c';
            color: $primary-color;
            position: absolute;
            right: $dp * 1.2;
            transform: rotate(45deg);
            font-size: $dp * 0.8;
            transition: transform 0.2s;
          }

          .btn-link {
            color: $white;
            text-decoration: none;
            text-align: left;
          }
        }

        &-body {
          background: $bg-color--light;
          font-size: $s_normal;
          padding: $dp * 1.2;

          @media only screen and (max-width: $bp) {
            background: $white;
          }

          p {
            font-size: $s_normal;

            a {
              color: $black;
              text-decoration: underline;
              font-family: 'Read-medium';
            }
          }
        }
      }

      &.panel-open {
        .card {
          &-header {
            &:before {
              transform: rotate(0deg);
            }
          }
        }
      }
    }
  }
}
