// ============================================================================
//
// COMPONENTS - DATEPICKER
//
// ============================================================================
.theme-green {
  .bs-datepicker-head {
    background: $yellow;
  }

  .bs-datepicker-body {
    table {
      td {
        &.week {
          span {
            color: $yellow;
          }
        }
        span.selected {
          background-color: $yellow;
        }
      }
    }
  }
}
