// ============================================================================
//
//    TABLE
//
// ============================================================================

.table {
  width: 100%;

  &__header {
    width: 100%;
    display: flex;
    flex-direction: row;
    background: $table-bg__header;
    height: $dp * 3.6;
    align-items: center;
    padding: 0 $dp * 2;

    @media only screen and (max-width: $bp) {
      display: none;
    }

    p {
      color: $table-color__header;
      font-size: $s_normal;
      padding-right: $dp;
      overflow: hidden;
      flex-wrap: nowrap;
      text-overflow: ellipsis;

      @media only screen and (max-width: $bp) {
        display: none;
      }
    }
  }

  &__body {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: $dp * 2.8;
    overflow-y: auto;
    height: $dp * 45;

    @media only screen and (max-width: $bp) {
      margin-bottom: 0;
      height: auto;
    }

    &__section {
      @media only screen and (max-width: $bp) {
        margin-bottom: $dp * 1.2;
        -webkit-box-shadow: 0px 3px 20px -10px rgba(0, 0, 0, 0.5);
        -moz-box-shadow: 0px 3px 20px -10px rgba(0, 0, 0, 0.5);
        box-shadow: 0px 3px 20px -10px rgba(0, 0, 0, 0.5);
      }

      &__date {
        display: none;
        width: 100%;
        background: $table-bg__header;
        height: $dp * 3.6;
        align-items: center;
        padding: 0 $dp * 1.5;
        color: $table-color__header;

        @media only screen and (max-width: $bp) {
          display: flex;
        }
      }
    }

    &__row {
      width: 100%;
      display: flex;
      flex-direction: column;
    }

    &__title {
      width: 100%;
      display: flex;
      flex-direction: row;
      padding: $dp $dp * 2;
      border-bottom: $table-border-line;

      @media only screen and (max-width: $bp) {
        padding: $dp * 0.5 $dp * 1.5;
        align-items: flex-start;
        background: $table-bg__body;
      }

      &:hover {
        background: $table-bg__body--hover;
        cursor: pointer;

        @media only screen and (max-width: $bp) {
          background: $table-bg__body;
        }
      }

      &__group {
        display: flex;
        flex-direction: row;

        &:last-child {
          justify-content: flex-end;
        }

        &__col {
          padding-right: $dp;
          font-size: $s_normal;

          &:last-child {
            padding: 0;
          }
        }

        @media only screen and (max-width: $bp) {
          flex-direction: column;
        }
      }
    }

    &__detail {
      background: $table-color-icon;
      padding: $dp * 1.5;

      @media only screen and (max-width: $bp) {
        background: $table-bg__body;
        padding: $dp * 0.5 $dp;
        border-bottom: $table-border-line;
      }

      &__row {
        display: flex;
        flex-direction: row;
        align-items: flex-end;

        p {
          @media only screen and (max-width: $bp) {
            font-size: $s_small;
          }

          &:first-child {
            text-align: right;
          }
        }
      }
    }
  }

  &__actions {
    display: flex;
    flex-direction: row;

    @media only screen and (max-width: $bp) {
      display: none;
    }

    &:hover {
      text-decoration: none;
      color: $black;
    }

    p {
      text-decoration: underline;
      margin-left: $dp * 0.5;
    }
  }
}
