// ============================================================================
//
// STATUS BAR
//
// ============================================================================

app-hire-deposit-status-bar {
  width: 100%;
}

.status-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;

  .step {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__circle {
      width: $dp * 2.4;
      height: $dp * 2.4;
      border-radius: 50%;
      color: $gray-3;
      border: 1px solid $gray-3;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: $dp;
      background: $white;
      z-index: 12;
      -webkit-box-shadow: 0px 0px 0px 10px $white;
      -moz-box-shadow: 0px 0px 0px 10px $white;
      box-shadow: 0px 0px 0px 10px $white;

      @media only screen and (max-width: $bp) {
        -webkit-box-shadow: 0px 0px 0px 10px $white-2;
        -moz-box-shadow: 0px 0px 0px 10px $white-2;
        box-shadow: 0px 0px 0px 10px $white-2;
        background: $white-2;
      }
    }

    &__number {
      display: flex;
    }

    &__icon {
      display: none;
      font-size: $s_xtiny;
    }

    p {
      text-align: center;
      color: $gray-3;
    }

    &.active {
      .step__circle {
        border: 1.5px solid $black;
        color: $black;
        font-family: 'Read-bold';
      }

      p {
        color: $black;
        font-family: 'Read-bold';
      }
    }

    &.completed {
      .step {
        &__circle {
          border: 1.5px solid $yellow;
          color: $white;
          background: $yellow;
          font-family: 'Read-bold';
        }

        &__number {
          display: none;
        }

        &__icon {
          display: flex;
        }
      }

      p {
        color: $yellow;
        font-family: 'Read-bold';
        text-transform: none;
        font-size: $s_small;
      }
    }
  }

  .line {
    height: $dp * 0.2;
    background: $gray-3;
    position: absolute;
    top: $dp * 1.2;
    right: $dp * 4;
    left: $dp * 5;
    z-index: 11;

    &.partial {
      background: linear-gradient(to right, $yellow 50%, $gray-3 50%);
    }

    &.completed {
      background: $yellow;
    }
  }
}
