// ============================================================================
//
//    BASE - SETTINGS
//
// ============================================================================
// =============================================================================
// Default Radius, Sizes, Spaces, floats
// =============================================================================

$default-radius: 5px;
$default-padding: 10px;
$dp: $default-padding;
$maxwidth: 1200px;
$maxwidth-login: 264px;
$breakpoint: 799px;
$bp: $breakpoint;

// =============================================================================
// Colors
// =============================================================================

// Generic GrayColors
$black: #000000;
$black-7: #333333;
$black-7-light: #555555;
$gray-9: #666666;
$gray-7: #999999;
$gray-3: #cccccc;
$gray-3-light: #e1e1e1;
$white-2: #f0f0f0;
$white: #ffffff;

// Generic Colors
$yellow: #ffcc33;
$yellow-light: #ffde00;
$jaune-miel: #db8a06;
$orange-atacama: #d57800;
$orange-valancla: #e35205;
$brun-terracota: #8d3f2b;
$rouge-millesime: #572d2d;
$cuivre-sobre: #956c58;
$bleu-celadon: #3eb1c8;
$bleu-topaze: #00a7b5;
$vert-barents: #007377;
$vert-zxfk: #5a8d84;
$gris-baltique: #a4bcc2;
$blanc-albatre: #e8e6df;
$red: #bd2420;
$green: #59a618;
$red-light: #bd242010;
$primary-color: $yellow;

// Status color
$danger-color: $red;
$success-color: $green;
// $info-color: $first-color;
// $warning-color: $yellow;

// Input color
$label-color: $gray-9;
$label-color--focus: $black-7;
$label-color--disabled: rgba($gray-3, 0.5);
$placeholder-color: $gray-7;
$placeholder-color--disabled: rgba($gray-7, 0.4);
$background--focus: #fef7ea;
$background--error: $red-light;
$background--disabled: $gray-3;

// Border and line color
$border-color: $gray-3;
$border-radius-input: 0px;
$border-radius-checkbox: 0px;
$border-line: 2px solid $border-color;
$border-line--focus: 2px solid $primary-color;
$border-line--error: 2px solid $danger-color;
$border-line--disabled: 2px solid rgba($gray-3, 0.4);
$border-line-checkbox: 2px solid $border-color;
$border-line-radio: 1.5px solid $border-color;
$border-line-radio--selected: 1.5px solid $primary-color;
$border-line-radio--disabled: 1.5px solid rgba($border-color, 0.4);
$border-line-radio--error: 1.5px solid $danger-color;
// $border-line--success: 1px solid  $success-color;

// Button color
$primary-button-bg: $primary-color;
$primary-button-bg--hover: $yellow-light;
$primary-button-bg--disabled: $white-2;
$primary-button-color--disabled: #a0a0a5;
$secondary-button-bg: transparent;
$secondary-button-bg--hover: $gray-3-light;
$terciary-button-bg: $gray-3;
$terciary-button-bg--hover: $gray-3-light;

// Background color
$bg-color--dark: $black-7;
$bg-color--light: $white-2;

// Table color
$table-bg__header: $gray-7;
$table-bg__body: $white;
$table-bg__body--hover: $white-2;
$table-color__header: $white;
$table-color-icon: $gray-3;
$table-border-line: 1px solid $white-2;

// =============================================================================
// Fonts
// =============================================================================

// Font-family
$font-primary: 'Read-regular', sans-serif;
$base-font-size: 20px;
$base-line-increment: 1.2;
$base-font-family: $font-primary;
$font-input: 'Life-regular';

// Font size
$font-sizes: (
  10px,
  // $s_xtiny
    12px,
  // footer - $s_tiny
    14px,
  // h5, h6, buttons - $s_small
    16px,
  //$base-font-size - h4 - $s_normal
    20px,
  // h3 - $s_medium
    24px,
  // h2 - $s_large
    36px,
  // h1 - $s_xlarge
);

$s_xtiny: nth($font-sizes, 1);
$s_tiny: nth($font-sizes, 2);
$s_small: nth($font-sizes, 3); // h5, h6
$s_normal: nth($font-sizes, 4); // $base-font-size - h4
$s_medium: nth($font-sizes, 5); // h3
$s_large: nth($font-sizes, 6); // h2
$s_xlarge: nth($font-sizes, 7); // h1

h1 {
  font-size: $s_xlarge;
  font-family: 'Life-regular';
  font-weight: normal;
  line-height: 1;

  @media only screen and (max-width: $bp) {
    font-size: $s_large;
  }
}
h2 {
  font-size: $s_large;
  font-family: 'Life-bold';
  font-weight: normal;
}
h3 {
  font-size: $s_medium;
}
h4 {
  font-size: $s_normal;
}
h5,
h6 {
  font-size: $s_small;
}
p {
  font-size: $s_small;
  font-family: $font-primary;

  @media only screen and (max-width: $bp) {
    font-size: $s_small;
  }
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
a {
  color: $black-7;
}
