.personal-data {
  &.max-size {
    @media only screen and (max-width: $bp) {
      background: $white-2;
      min-height: calc(100vh - 96px);
    }
  }

  .tabs-block-size {
    width: $dp * 58.8;

    @media only screen and (max-width: $bp) {
      width: 100%;
    }
  }
}
