// ============================================================================
//
//    PAGE - GLOBAL POSITION
//
// ============================================================================

.global-position {
  background-repeat: no-repeat;
  background-position: right center;
  background-size: 50%;
  min-height: calc(100vh - 304px);

  @media only screen and (max-width: $bp) {
    background: $bg-color--light !important;
  }

  .main {
    justify-content: flex-end;

    @media only screen and (max-width: $bp) {
      flex-direction: column;
      margin: 0;
    }
  }

  .main-col {
    width: 50%;
    padding-right: $dp * 7.2;

    @media only screen and (max-width: $bp) {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 0;
      align-items: center;
      text-align: center;
    }

    &__body {
      width: 100%;

      &__product {
        background: $bg-color--dark;
        padding: $dp * 1.2 0;
        margin-bottom: $dp * 2.4;
        display: flex;
        flex-direction: row;

        @media only screen and (max-width: $bp) {
          padding: $dp * 0.8 0;
        }

        &__icon {
          width: $dp * 8;
          justify-content: center;
          border-right: 3px solid $white;
          display: flex;

          span {
            font-size: $s_large;
            color: $yellow;
            display: flex;
            align-items: center;
          }
        }

        &__content {
          padding: 0 $dp * 1.2 0 $dp * 2.8;
          display: flex;
          width: 100%;
          flex-direction: row;
          justify-content: space-between;
          color: $white;

          @media only screen and (max-width: $bp) {
            padding-left: $dp * 1.6;
          }
        }

        &__text {
          width: calc(100% - 100px);
          text-align: left;
          font-family: 'Life-regular';
        }

        &__name {
          color: $white;
          font-family: 'Life-regular';
        }

        &__balance {
          color: $white;
          font-family: 'Life-regular';
        }

        &__detail {
          display: flex;
          justify-content: flex-end;
          align-items: flex-start;

          &__link {
            color: $primary-color;
            text-decoration: none;
            font-family: 'Life-regular';
            font-size: $s_small;
          }
        }

        &__process {
          text-align: left;

          .main-col__body__product__balance {
            font-family: 'life-italic';
          }
        }
      }
    }
  }

  .aside-col {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @media only screen and (min-width: $bp) {
      background-image: none !important;
    }

    @media only screen and (max-width: $bp) {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 0;
      align-items: center;
      text-align: center;
      background-repeat: no-repeat;
      background-position: left center;
      background-size: cover;
    }

    &__body {
      display: flex;
      text-align: right;
      align-items: flex-end;
      flex-direction: column;
      max-width: $dp * 35;

      @media only screen and (max-width: $bp) {
        max-width: 100%;
      }
    }

    &__text {
      @media only screen and (max-width: $bp) {
        display: flex;
        flex-direction: column;
        text-align: center;
        padding: $dp * 3;
        height: $dp * 44.5;
      }

      p {
        font-size: $s_tiny;
        text-transform: uppercase;
        margin-bottom: $dp * 0.5;
      }

      h2 {
        font-family: 'Life-regular';
        text-transform: uppercase;
        margin-bottom: $dp * 3;
        font-size: $s_normal;
      }
    }

    a {
      @media only screen and (max-width: $bp) {
        width: 100%;
        min-height: $dp * 4.5;
        background: $bg-color--dark;
        color: $yellow;
        text-transform: uppercase;
      }
    }
  }
}
