// ICON FONTS

@font-face {
  font-family: 'icon-font';
  src: url('assets/fonts/icon-font.eot?4brdrb');
  src: url('assets/fonts/icon-font.eot?4brdrb#iefix') format('embedded-opentype'),
    url('assets/fonts/icon-font.ttf?4brdrb') format('truetype'),
    url('assets/fonts/icon-font.woff?4brdrb') format('woff'),
    url('assets/fonts/icon-font.svg?4brdrb#iconFont') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^='ico-'],
[class*=' ico-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icon-font' !important;
  font-size: $s_normal;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

