// ============================================================================
//
//    PAGE - TRANSFER
//
// ============================================================================

.transfer {
  display: flex;
  flex-direction: row;

  @media only screen and (max-width: $bp) {
    background: $bg-color--light;
    min-height: calc(100vh - 184px);
  }

  .main-col {
    max-width: calc(100% - 320px);

    @media only screen and (max-width: $bp) {
      width: 100%;
      max-width: 100%;
    }
  }

  .aside-col {
    width: $dp * 29;
    height: $dp * 57;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
    display: flex;
    flex-direction: column;
    text-align: right;
    align-items: flex-end;
    padding: $dp * 2;

    @media only screen and (max-width: $bp) {
      display: none;
    }

    p {
      font-size: $s_tiny;
      text-transform: uppercase;
      margin-bottom: $dp * 0.5;
      color: $white;
    }

    h2 {
      font-family: 'Life-regular';
      text-transform: uppercase;
      margin-bottom: $dp * 3;
      color: $white;
      font-size: $s_normal;
    }
  }

  form {
    width: $dp * 36;

    @media only screen and (max-width: $bp) {
      width: 100%;
    }
  }

  .group-button {
    margin-top: $dp * 3;

    @media only screen and (max-width: $bp) {
      display: none;
    }
  }

  .confirm-transfer {
    width: $dp * 36;

    @media only screen and (max-width: $bp) {
      width: 100%;
    }

    label {
      margin-bottom: 0;
    }

    p {
      font-family: 'Life-bold';
    }
  }
}
