// ============================================================================
//
//    FOOTER
//
// ============================================================================
footer {
  background: $white;
  width: 100%;
  display: flex;
  flex-direction: column;

  @media only screen and (max-width: $bp) {
    display: none;
  }

  .footer-row {
    height: $dp * 6;
    display: flex;
    align-items: center;
    background: $bg-color--light;

    .max-size {
      justify-content: space-between;
      align-items: center;
      flex-direction: row;
    }

    p {
      font-size: $s_small;
      font-family: 'Life-regular';

      a {
        color: $black-7;
        text-decoration: underline;
      }
    }

    &:last-child {
      background: $bg-color--dark;

      p,
      a {
        color: #f7f6f5;
      }
    }

    .social-media {
      display: flex;
      flex-direction: row;

      a {
        margin-left: $dp * 1.5;

        &:hover {
          color: $black-7;
        }

        span {
          font-size: $dp * 1.5;
        }
      }
    }

    .footer-links {
      font-family: 'Life-regular';
      font-size: $s_small;
      display: flex;
      flex-wrap: wrap;

      a {
        text-decoration: none;
        padding: 0 $dp * 1.2;
        border-right: 1px solid rgba($color: #f7f6f5, $alpha: 0.5);

        &:last-child {
          border: none;
          padding-right: 0;
        }
      }
    }
  }
}
