// ============================================================================
//
//    LOGIN
//
// ============================================================================

// Width screen
.max-size-login {
  width: 100%;
  max-width: $maxwidth-login;
  display: flex;
  margin-top: 10vh;

  @media only screen and (max-width: $bp) {
    max-width: none;
  }
}

app-login-form {
  width: 100%;
}

.login {
  width: 100%;
  min-height: 100vh;
  background: $bg-color--light;
  background-image: url('assets/img/img-login.jpg');
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: right center;

  @media only screen and (max-width: $bp) {
    background-image: none;
  }

  &__header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: $dp * 3.6 0;

    a {
      &:hover {
        text-decoration: none;
      }
    }

    @media only screen and (max-width: $bp) {
      justify-content: center;
    }

    .logo-desktop {
      width: $dp * 7.6;

      @media only screen and (max-width: $bp) {
        display: none;
      }
    }
  }

  .group-input {
    flex-direction: column;
    margin-bottom: $dp * 3;

    label {
      font-family: 'Life-regular';
      color: $gray-9;
      display: flex;
      justify-content: space-between;
      align-items: baseline;

      a {
        font-family: 'Read-italic';
        font-size: $s_tiny;
        text-decoration: underline;

        &:hover {
          color: $gray-9;
        }
      }
    }

    input {
      height: $dp * 4.8;
    }

    .error-text {
      display: none;
      position: absolute;
      bottom: $dp * -2;
      color: $danger-color;
      font-size: $s_tiny;
      font-family: 'Life-regular';
      align-items: baseline;

      span {
        margin-right: $dp * 0.5;
        font-size: $s_tiny;
      }
    }
  }

  .group-button {
    margin-top: $dp * 3;

    @media only screen and (max-width: $bp) {
      display: flex;
    }

    a {
      &:hover {
        text-decoration: none;
        color: initial;
      }
    }

    &.button-desktop {
      @media only screen and (max-width: $bp) {
        display: none;
      }
    }
  }

  .main__button {
    a {
      font-size: $s_normal;
    }
  }
}
