// ============================================================================
//
//    PAGE - VIRTUAL-MAILBOX
//
// ============================================================================

.virtual-mailbox {
  @media only screen and (max-width: $bp) {
    min-height: calc(100vh - 96px);
  }

  .main__body {
    flex-direction: column;
  }

  form {
    display: flex;
    flex-direction: row;

    @media only screen and (max-width: $bp) {
      flex-direction: column;
    }

    .group-input {
      width: $dp * 19.2;
      margin-right: $dp * 1.8;

      @media only screen and (max-width: $bp) {
        width: 100%;
        margin-right: 0;
      }
    }

    & + .tabs-block--desktop {
      width: $dp * 58.8;

      @media only screen and (max-width: $bp) {
        width: 100%;
        margin-top: $dp * 4;
      }
    }

    .group-button {
      margin-bottom: $dp * 2;
      align-items: flex-end;

      @media only screen and (max-width: $bp) {
        display: none;
      }

      button {
        height: $dp * 2.4;
      }
    }
  }
}
