// ============================================================================
//
//  MODAL
//
// ============================================================================

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba($color: $black, $alpha: 0.66);
  display: flex;
  justify-content: center;
  align-items: center;

  .modal-content {
    position: relative;
  }

  .max-size-form {
    background: $white;
    border: $border-line--focus;
    padding: $dp * 7 $dp * 7 $dp * 5 $dp * 7;
    align-items: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    position: relative;

    @media only screen and (max-width: $bp) {
      padding: $dp * 7 $dp * 2 $dp * 2 $dp * 2;
    }
  }

  &__close {
    position: absolute;
    top: $dp * 2;
    right: $dp * 2;
    cursor: pointer;
  }

  &__icon {
    font-size: $dp * 4.8;
    margin-bottom: $dp * 2;

    &.ico-alert {
      color: $danger-color;
    }

    &.ico-check-filled {
      color: $success-color;
    }
  }

  &__title {
    font-family: 'Life-bold';
  }

  &__text {
    display: flex;
    flex-direction: column;
    margin: $dp * 2 0 0 0;
    text-align: center;
    font-size: $s_normal;

    @media only screen and (max-width: $bp) {
      margin: $dp * 2 0;
    }

    p {
      margin-top: $dp * 2;
      font-size: $s_normal;

      &:first-child {
        margin: 0;
      }

      a {
        &:hover {
          color: $black;
        }
      }
    }

    & + input {
      margin-top: $dp * 5;

      @media only screen and (max-width: $bp) {
        margin-top: $dp * 2;
      }
    }
  }

  &__text-info {
    font-size: $s_tiny;
    color: $gray-7;
    font-family: 'Read-italic';
  }

  &__contacts {
    margin-top: $dp * 4;
    width: 100%;
    text-align: center;

    @media only screen and (max-width: $bp) {
      margin-top: $dp * 1.5;
    }

    p {
      font-size: $s_normal;
      margin-bottom: $dp;

      a {
        color: $black;
      }
    }
  }

  &__mid-error {
    font-size: $s_tiny;
    color: $black-7-light;
    font-family: 'Read-italic';
    margin-top: $dp * 6;
    text-align: center;
    margin-bottom: $dp * -3;
  }

  .group-input {
    margin-top: $dp * 2;
    margin-bottom: 0;
  }

  input[type='text'] {
    width: $dp * 26.5;
    height: $dp * 4.8;

    & + .error-text {
      width: $dp * 26.5;
    }
  }

  .new-sms {
    margin-top: $dp;
    font-size: $s_tiny;
    color: $black-7-light;
    text-decoration: underline;
    font-family: 'Read-regular';

    &:hover {
      color: $black-7-light;
    }
  }

  &__group-button {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .modal__btn {
      width: 48%;
    }
  }

  &__btn {
    width: $dp * 26.5;
    margin-top: $dp * 6;

    @media only screen and (max-width: $bp) {
      width: 100%;
      margin-top: $dp * 4;
    }
  }

  .modal-cookies {
    .modal-content {
      background: $white;
      border: $border-line--focus;
      padding: $dp * 4 $dp * 7 $dp * 4 $dp * 7;
      border-radius: 0rem;
      font-family: 'Life-regular';
      position: relative;

      @media only screen and (max-width: $bp) {
        padding: $dp * 2;
      }
    }

    &__title {
      font-family: 'Life-bold';
      margin-bottom: $dp * 5;
    }

    &__body {
      p {
        font-family: 'Read-regular';
        font-size: $s_normal;
        margin-bottom: $dp * 1.5;

        &.subtitle {
          font-family: 'Read-medium';
          margin-top: $dp * 3;
        }

        a {
          font-family: 'Read-medium';
          text-decoration: underline;

          &:hover {
            color: $black;
          }
        }
      }

      .group-button {
        margin-top: $dp * 5;
        margin-bottom: $dp * 5;

        button {
          width: 48%;

          &:nth-child(2) {
            margin: 0;
          }
        }
      }

      &__cookies {
        &__options {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: flex-end;
          margin: $dp * 4 0;

          @media only screen and (max-width: $bp) {
            justify-content: space-between;
          }

          a {
            margin-right: $dp * 4;
            text-transform: uppercase;
            font-family: 'Life-bold';
            text-align: center;
            display: flex;

            @media only screen and (max-width: $bp) {
              font-size: $s_small;
              width: 49%;
              margin: 0;
              justify-content: center;
            }

            &:hover {
              color: $black;
            }

            img {
              transform: rotate(90deg);
              margin-right: $dp * 0.5;
            }
          }

          button {
            @media only screen and (max-width: $bp) {
              width: 49%;
            }
          }
        }
      }
    }
  }
}
