// ============================================================================
//
//    BASE - GENERAL
//
// ============================================================================

//100% view height minus header and footer height
.router-outlet {
  height: 100%;
  min-height: calc(100vh - (185px + 120px));

  &.app-screen {
    .max-size {
      min-height: 100vh !important;
    }
  }
}
// Width screen
.max-size {
  width: 100%;
  max-width: $maxwidth;
  display: flex;
  margin: 0 auto;
  flex-direction: column;

  @media only screen and (max-width: $maxwidth + $dp * 2) {
    padding: 0 $dp * 2.4;
  }

  @media only screen and (max-width: $bp) {
    padding: $dp * 2.4;
  }
}

.main {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-top: $dp * 6;
  margin-bottom: $dp * 6;
  justify-content: space-between;

  @media only screen and (max-width: $bp) {
    margin-top: 0;
    margin-bottom: $dp * 9;
  }

  .main-col {
    width: 100%;

    @media only screen and (max-width: 1099px) {
      width: 100%;
      max-width: 100%;
    }
  }

  &__header {
    display: flex;
    width: 100%;

    &__desktop {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      margin-bottom: $dp * 4.8;

      @media only screen and (max-width: $bp) {
        display: none;
      }
    }

    &__mobile {
      display: none;
      flex-direction: row;
      justify-content: center;
      width: 100%;
      margin-bottom: $dp * 2.4;

      &__col {
        width: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;

        .ico-search-1 {
          position: absolute;
          bottom: $dp * -0.5;
          left: $dp * -5;
          color: $gray-7;
          background: $white;
          border-radius: 50%;
          border: 1px solid $gray-7;
          width: $dp * 3.8;
          height: $dp * 3.8;
          display: flex;
          justify-content: center;
          align-items: center;

          @media only screen and (max-width: 375px) {
            bottom: $dp * 2.5;
            left: 0;
          }
        }
      }

      h1 {
        text-align: center;
        font-family: 'Life-bold';
      }
      p {
        text-align: center;
        font-size: $s_normal;
      }

      @media only screen and (max-width: $bp) {
        display: flex;
      }
    }

    &__title {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      &__col {
        padding: 0 $dp * 3;
        border-left: 6px solid $black-7;

        &:first-child {
          padding-left: 0;
          border: 0;
        }

        p {
          font-size: $s_normal;
          font-family: 'Life-regular';
        }
      }
    }

    &__buttons {
      display: flex;
      flex-direction: column;

      button {
        &:first-child {
          margin-bottom: $dp;
        }
      }
    }
  }

  &__body {
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-size: $s_small;

    @media only screen and (max-width: $bp) {
      flex-direction: column;
    }
  }

  &__button {
    display: none;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    padding: $dp * 2.4;
    background: $bg-color--light;
    font-size: $s_small;
    z-index: 10;

    @media only screen and (max-width: $bp) {
      display: flex;

      & .hidden {
        display: none;
      }
    }

    &.group-button {
      justify-content: space-between;
      flex-direction: row;
      display: none;

      @media only screen and (max-width: $bp) {
        display: flex;
      }

      button {
        width: 48%;
        font-size: $s_small;
      }
    }
  }

  .col--2 {
    width: 49%;

    @media only screen and (max-width: $bp) {
      width: 100%;
    }
  }
}

.semibold {
  font-family: 'Read-medium';
}

.underline {
  text-decoration: underline;
}
