// ============================================================================
//
// LOADING
//
// ============================================================================

.loading {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  background: rgba($white, $alpha: 0.5);

  &__img {
    width: $dp * 7;
  }
}
