// ============================================================================
//
//    ERROR
//
// ============================================================================

.error-msg {
  width: $dp * 50;
  height: auto;
  display: flex;
  justify-content: center;
  margin: $dp * 5 auto;

  @media only screen and (max-width: $bp) {
    width: 100%;
    max-width: $dp * 50;
    margin: 0 auto;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;

    a {
      @media only screen and (max-width: $bp) {
        max-width: $dp * 36;
      }
    }
  }

  &__icon {
    font-size: $dp * 7;
    color: $danger-color;

    @media only screen and (max-width: $bp) {
      font-size: $dp * 5;
    }
  }

  &__text {
    margin: $dp * 5 0;

    @media only screen and (max-width: $bp) {
      margin-top: $dp * 3;
    }

    p {
      font-size: $s_normal;
      text-align: center;
      margin-top: $dp * 2;

      &:first-child {
        margin: 0;
      }

      a {
        &:hover {
          color: $black;
        }
      }

      @media only screen and (max-width: $bp) {
        font-size: $s_normal;
      }
    }
  }
}
