//
// TOOLTIP
//
// ============================================================================

.tooltip {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  margin-left: $dp;
  position: relative;
  z-index: auto;
  opacity: 1;

  .info {
    width: $dp * 1.5;

    &:hover {
      & + .detail-info {
        display: flex;
      }
    }
  }

  .detail-info {
    position: absolute;
    width: $dp * 22;
    background: $gray-3-light;
    top: $dp * 3.5;
    display: none;
    justify-content: center;
    padding: $dp;
    z-index: 9;

    @media only screen and (max-width: $bp) {
      width: $dp * 14;
    }

    .arrow {
      position: absolute;
      top: $dp * -1;
      display: flex;
      width: 0px;
      height: 0px;
      border-left: $dp solid transparent;
      border-right: $dp solid transparent;
      border-bottom: $dp solid $gray-3-light;
    }

    p {
      font-size: $s_tiny;
      font-family: 'Read-regular';
    }
  }
}
