// ============================================================================
//
//    PAGE - TRANSACTION
//
// ============================================================================

.transactions {
  display: flex;
  flex-direction: row;

  @media only screen and (max-width: $bp) {
    background: $bg-color--light;
    min-height: calc(100vh - 184px);
  }

  .main-col {
    max-width: calc(100% - 320px);

    @media only screen and (max-width: 1024px) {
      max-width: 100%;
    }
  }

  .aside-col {
    width: $dp * 29;
    height: $dp * 57;
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
    display: flex;
    flex-direction: column;
    text-align: right;
    align-items: flex-end;
    padding: $dp * 2;

    @media only screen and (max-width: 1024px) {
      display: none;
    }

    p {
      font-size: $s_tiny;
      text-transform: uppercase;
      margin-bottom: $dp * 0.5;
    }

    h2 {
      font-family: 'Life-regular';
      text-transform: uppercase;
      margin-bottom: $dp * 3;
      font-size: $s_normal;
    }
  }

  // Estilos personalizados de la tabla de Cuenta Ahorro
  .table {
    &__header {
      p {
        &:first-child {
          width: 30%;
        }

        &:nth-child(2) {
          width: calc(70% - 240px);
        }

        &:nth-child(3) {
          width: $dp * 12;
          text-align: right;
        }

        &:nth-child(4) {
          width: $dp * 12;
          text-align: right;
          padding: 0;
        }
      }
    }

    &__body {
      &__title {
        &__group {
          &:first-child {
            width: $dp * 8.5;

            @media only screen and (max-width: $bp) {
              display: none;
            }
          }

          &:nth-child(2) {
            width: calc(30% - 85px);
            justify-content: center;

            @media only screen and (max-width: $bp) {
              width: $dp * 3;
              margin-right: $dp * 1.5;
            }

            p {
              color: $table-color-icon;
              font-size: $dp * 1.8;
            }
          }

          &:nth-child(3) {
            width: calc(70% - 240px);

            @media only screen and (max-width: $bp) {
              width: calc(50% - 15px);
            }

            .table__body__title__group__col {
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          }

          &:nth-child(4) {
            width: $dp * 24;
            text-align: right;

            @media only screen and (max-width: $bp) {
              width: calc(50% - 30px);
            }

            .table__body__title__group__col {
              width: 50%;

              @media only screen and (max-width: $bp) {
                width: 100%;
                padding: 0;
              }

              &:last-child {
                @media only screen and (max-width: $bp) {
                  font-size: $s_small;
                  color: $table-color-icon;
                }
              }
            }
          }
        }
      }

      &__detail {
        &__row {
          p {
            &:first-child {
              width: 20%;
              text-align: right;
              margin-right: $dp * 4;

              @media only screen and (max-width: $bp) {
                color: $gray-9;
                width: 30%;
                margin-right: $dp * 2;
              }
            }

            &:last-child {
              width: calc(80% - 40px);

              @media only screen and (max-width: $bp) {
                width: calc(60% - 20px);
                color: $black-7;
              }
            }
          }
        }
      }
    }
  }
}

// Buscador de movimientos
.transaction-search {
  @extend .transactions;

  .main__body {
    flex-direction: column;

    form {
      display: flex;
      flex-direction: row;

      @media only screen and (max-width: $bp) {
        flex-direction: column;
      }

      .group-input {
        width: $dp * 19.2;
        margin-right: $dp * 2.4;

        @media only screen and (max-width: $bp) {
          width: 100%;
          margin-right: 0;
        }
      }

      .group-button {
        margin-bottom: $dp * 2;
        align-items: flex-end;

        @media only screen and (max-width: $bp) {
          display: none;
        }

        button {
          height: $dp * 2.4;
        }
      }
    }
  }
}
